<template>
    <ul class="star-list">
        <li :class="{'current':val>=item,'half':(val<item&&val+1>item)}"
            :key="index"
            v-for="(item,index) in startList"></li>
    </ul>
</template>

<script>

    export default {
        props: {
            value: {
                type: Number
            }
        },
        mounted() {

        },
        data() {
            return {
                val:0,
                startList:[1,2,3,4,5]
            };
        },
        watch: {
            value:{
                handler(nv,ov){
                    this.val = nv
                },
                immediate:true
            },
            val(nv){
                if(nv){
                    this.$emit('input',nv)
                }else{
                    this.$emit('input',0)
                }
            }
        },
        computed:{
        },
        methods: {

        }
    };
</script>
