<template>
    <ul class="step-list">
        <!--ed:完成的  current当前正在进行的-->
        <li class="step-item"
            :class="{'current':curStep===item.step&&item.step!==stepList.length,'ed':curStep===stepList.length||curStep>item.step}"
            :key="index"
            v-for="(item,index) in stepList">
            <div class="step-item-head">
                <span class="num"></span>
                <div class="step-item-line"></div>
            </div>
            <div class="step-item-content">
                {{item.name}}
            </div>
        </li>
    </ul>
</template>

<script>
    export default {
        props:{
            currentStep:{
                type:Number
            }
        },
        data: () => ({
            curStep:0,
            stepList:[
                {name:'网络检测',step:1},
                {name:'耳机/音响检测',step:2},
                {name:'麦克风检测',step:3},
                {name:'检测完成',step:4}
            ]
        }),



        methods: {



        },
        mounted() {

        },
        computed: {},
        components: {},
        watch:{
            currentStep:{
                handler(nv){
                    this.curStep = nv?nv:0
                },
                immediate:true
            }
        }
    }
</script>

<style lang="postcss" scoped>

</style>
