<template>
    <div class="change-child-box"
         v-if="show"
         @click="cancelBubble">
                    <div class="border-up">
                        <span></span>
                    </div>
                    <ul class="change-list">
                        <li v-for="item in studentList"
                            :key="item.student_id">
                            <img :src="item.head_portrait_url">
                            <div class="user-msg-box">
                                <p class="user-name">{{item.name}}</p>
                                <p class="user-age">{{item.grade_type_name || '-'}}</p>
                            </div>
                            <button :class="{'current':childInfo.student_id!==item.student_id}"
                                    :style="(childInfo.student_id===item.student_id)?{cursor:'not-allowed'}:{}"
                                    @click="loginByStudentId(item.student_id)">点击切换</button>
                        </li>
                    </ul>
                </div>



</template>

<script>
    import {mapActions,mapGetters,mapMutations} from 'vuex'
    import { Message } from 'element-ui'
    export default {
        inject:['routerReload'],
        props:{
            show:{
                type:Boolean
            }
        },
        data: () => ({
            studentList:[],
            submitAble:false
        }),
        methods:{
            ...mapActions('child',['getLearningUserStudentSimpleListActions']),
            ...mapActions('account',['triggerStudentActions']),
            ...mapMutations(['updateCourseSelectList','updateCourseStatus']),
            cancelBubble(e){
                e.stopPropagation()
            },
            loginByStudentId(studentId){
                if(this.submitAble || this.childInfo.student_id===studentId){
                    return
                }
                let params = {student_id: studentId}
                this.submitAble = true
                this.triggerStudentActions(params).then((res)=>{
                    this.submitAble = false
                    let {error_code,data} = res;
                    if(!error_code){
                        this.$store.dispatch('account/setStudentIdCookie',studentId);
                        this.$message.success({ message: '切换成功'});
                        this.$emit('changeSuccess');
                        this.$bus.$emit('getHomeCard',true);
                        this.goView('sign_in_success');
                        this.updateCourseSelectList([]);
                        this.updateCourseStatus('');
                        this.routerReload();
                    }
                },(err)=>{
                    this.submitAble = false;
                })
            },
            getStudentList(){
                this.getLearningUserStudentSimpleListActions().then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        let list = data;
                        this.studentList = list;
                    }
                },(err)=>{})
            }
        },
        mounted(){},
        watch:{
            show(nv){
                if(nv){
                    this.getStudentList();
                }
            }
        },
        computed: {
            ...mapGetters('child', ['childInfo'])
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
