<template>
    <div class="aside-right" style="position: fixed;right:0;">
        <div class="stu-evaluate" @click="goView('downloadApp')">
            <img src="@/assets/img/code-top.png" alt="">
            <img class="download" src="@/assets/img/app-download.png" alt="">
        </div>
        <div class="xcx-box" @click="openEwm(1)">
            <img src="@/assets/img/code-middle.png" alt="">
            <img class="xcx" src="@/assets/img/xcx.jpg" alt="">
        </div>
        <div class="gzh-box" @click="openEwm(2)">
            <img src="@/assets/img/code-bottom.png" alt="">
            <img class="gzh" src="@/assets/img/gzh.jpg" alt="">
        </div>
    </div>
</template>

<script>
    import helper from '@/assets/js/helper'
    export default {
        data: () => ({

        }),
        methods:{
            openEwm(type){
                this.$bus.$emit('openEwm',type)
            },
            goEvalTest(){
                this.$router.push({
                    name:'beforeTest'
                })
            },
            goDownload(){
                let popup = window.open('about:blank', '_blank')
                popup.document.write('<h2>加载中</h2>')
                popup.location = 'https://www.google.cn/chrome/'
            }
        },
        computed: {
            showDownloadBtn(){
                let res = false
                let broswerTester = helper.browserTester
                if(!broswerTester.chrome() && !broswerTester.safari()){
                    res = true
                }else{
                    //edge也要显示下载按钮
                    if(broswerTester.edge()){
                        res = true
                    }else{
                        res = false
                    }
                }
                return res
            }
        },
        components: {},
        mounted() {

        }
    }
</script>

<style scoped lang="postcss">
    .browserDownload{
        width:6rem;
        height:6rem;
        text-align: center;
        background:rgba(0,0,0,1);
        border-radius:0.67rem;
        opacity:0.55;
        color:#fff;
        position: relative;
        display: inline-block;
        padding-top: 4rem;
        &:after{
            content:' ';
            display: block;
            position: absolute;
            width: 2.17rem;
            height: 2.25rem;
            background: url('~@/assets/img/dl-ico.png') no-repeat center;
            background-size: cover;
            top:1rem;
            left:2rem;
        }
    }
</style>
