<template>
    <div v-if="showDialog">
        <div class="dialog">
            <div class="dialog-mask"></div>
            <!--开始测试-->
            <div class="dialog-main device-test-1" v-if="curStep===0">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog">×</span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-1-content">
                        <img src="@/assets/img/networkImg.png" alt="">
                        <div class="title">请适配网络</div>
                        <div class="des">您没有适配过网络或网络环境发生了变化</div>
                        <div class="des">本步骤将会自动检测您的网络环境</div>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="closeDialog">一会再说</button>
                    <button class="btn btn-orange" @click="curStep++">开始测试</button>
                </div>
            </div>
            <!--网络检测-->
            <div class="dialog-main device-test-2" v-if="curStep===1">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog">×</span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-2-content">
                        <div class="top">
                            <step :current-step="curStep"></step>
                            <el-progress type="circle"
                                         :stroke-width="15"
                                         color="#FF7B00"
                                         style="margin-top:4rem"
                                         :percentage="circleValue"></el-progress>
                        </div>
                        <div class="bottom">
                            <div class="title">开始前的准备</div>
                            <p>1.建议使用有线网络比无线网络（WiFi）更稳定流畅</p>
                            <p>2.确认扬声器或耳机已连接并已开启音量</p>
                            <p>3.确认麦克风已连接</p>
                        </div>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-orange" @click="testNet">开始检测</button>
                </div>
            </div>
            <!--声音检测-->
            <div class="dialog-main device-test-3" v-if="curStep===2">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog">×</span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-3-content">
                        <step :current-step="curStep"></step>
                        <div class="device-box">
                            <span>选择播放设备</span>
                            <!--<input type="text"
                                   placeholder="默认设备/喇叭"
                                   class="ipt">-->
                            <el-select v-model="curHeadPhoneDeviceId"
                                       @change="setCurHeadPhone"
                                       placeholder="默认设备">
                                <el-option
                                        v-for="(item,index) in headPhones"
                                        :key="index"
                                        :label="item.label"
                                        :value="item.deviceId">
                                </el-option>
                            </el-select>
                            <a href="javascript:void(0)" @click="getMediadevices(1)">刷新新设备</a>
                            <!--{{curHeadPhoneDeviceId}}-->
                            <!--element.setSinkId(curHeadPhoneDeviceId)-->
                        </div>
                        <div class="audio-btn"
                             @click="playAudio"
                             style="cursor: pointer">
                            <img src="@/assets/img/playing.gif"
                                 v-show="playingAudio"
                                 class="playing playing-left"
                                 alt="">
                            <img src="@/assets/img/playing.gif"
                                 v-show="playingAudio"
                                 class="playing playing-right"
                                 alt="">
                            <!--<span v-show="playingAudio">播放中！！！</span>-->
                        </div>
                        <audio :src="mp3"
                               loop="loop"
                               style="display: none"
                               id="testAudio"
                               controls="controls"></audio>
                        <p>点击播放按钮，能否听到音乐声？</p>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="testOutput(2)">不可以</button>
                    <button class="btn btn-orange" @click="testOutput(1)">可以</button>
                </div>
            </div>
            <!--麦克风检测-->
            <div class="dialog-main device-test-3" v-if="curStep===3">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog">×</span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-3-content">
                        <step :current-step="curStep"></step>

                        <div class="device-box">
                            <span>选择麦克风</span>
                            <!--<input type="text"
                                   placeholder="默认设备"
                                   class="ipt">-->
                            <el-select v-model="curInputDeviceId"
                                       placeholder="默认设备">
                                <el-option
                                        v-for="(item,index) in inputDeviceList"
                                        :key="index"
                                        :label="item.label"
                                        :value="item.deviceId">
                                </el-option>
                            </el-select>
                            <a href="javascript:void(0)" @click="getMediadevices(2)">刷新新设备</a>
                        </div>


                        <audio autoplay
                               style="display: none"
                               id="au"
                               controls="controls"></audio>


                        <div class="input-btn" style="cursor: pointer">
                            <img src="@/assets/img/playing.gif"
                                 v-show="voiceSize"
                                 class="playing playing-left"
                                 alt="">
                            <img src="@/assets/img/playing.gif"
                                 v-show="voiceSize"
                                 class="playing playing-right"
                                 alt="">
                        </div>
                        <p>能否看到颜色条跳动，听到自己麦克风的声音？</p>
                        <p>若无法听到声音，看到跳动，请选择其他麦克风尝试</p>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="testInput(2)">不可以</button>
                    <button class="btn btn-orange" @click="testInput(1)">可以</button>
                </div>
            </div>
            <!--检测结果-->
            <div class="dialog-main device-test-4" v-if="curStep===4">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog">×</span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-4-content">
                        <step :current-step="curStep"></step>
                        <ul class="test-result-list">
                            <ul>
                                <li>
                                    <span class="tit">网络情况</span>
                                    <span class="res"
                                          :class="{'bad':!devicesStatusObj.net.level===3}">{{getNetStatus()}}</span>
                                </li>
                                <li>
                                    <span class="tit">声音</span>
                                    <span class="res"
                                          :class="{'bad':!devicesStatusObj.output.passed}">{{devicesStatusObj.output.passed?'正常':'异常'}}</span>
                                </li>
                                <li>
                                    <span class="tit">麦克风</span>
                                    <span class="res" :class="{'bad':!devicesStatusObj.input.passed}">{{devicesStatusObj.input.passed?'正常':'异常'}}</span>
                                </li>
                            </ul>
                        </ul>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="reTest">重新检测</button>
                    <button class="btn btn-orange" @click="closeDialog">关闭</button>
                </div>
            </div>
            <!--<button @click="curStep++" style="position: fixed;top:0;left:0;z-index:2000">next</button>-->
        </div>
    </div>
</template>

<script>
    import Cookie from 'cookie';
    import {mapActions,mapGetters} from 'vuex'
    import step from '@/components/devicesTest/step.vue'
    import mp3 from '@/assets/audios/music.mp3';
    import utils from '@/assets/js/helper';
    let scriptProcessor = null,
        audioContext = null,
        navMediaDevices = navigator.mediaDevices
    export default {
        props:{
            show:{
                type:Boolean
            }
        },
        data: () => ({
            inputAudio:null,
            curHeadPhoneDeviceId:'',
            curInputDeviceId:'',
            headPhones:[],//耳机/音响列表
            inputDeviceList:[],//麦克风列表
            audio:null,
            playingAudio:false,
            mp3:mp3,
            curStep:0,//0未开始检测 1.网络检测 2.耳机/音响检测 3.麦克风检测 4.检测完成
            showDialog:false,
            submitAble:false,
            circleValue: 0,
            devicesSteps: [
                {name: '网络检测',step:1},
                {name: '耳机/音响检测',step:2},
                {name: '麦克风检测',step:3},
                {name: '检测完成',step:4}
            ],
            devicesStatusObj:{
                net:{
                    passed:false,
                    level:1//1.优秀 2.良好 3.差
                },
                output:{
                    passed:false
                },
                input:{
                    passed:false
                }
            },
            hasConnectVoiceApi:false,
            voiceSize:0,
            downlink:0,
            audiooutputDevice:'',//音响设备
            audioinputDevice:''//麦克风设备
        }),
        //销毁页面时断开声音监听的连接
        destroyed(){
            this.disConnectVoiceProcess()
        },
        //回到页面时开启连接
        activated(){
            if(this.curStep===1){
                this.circleValue = 0
            }
            this.connectVoiceProcess()
        },
        //离开页面时断开声音监听的连接
        deactivated(){
            this.disConnectVoiceProcess()
        },
        methods: {
            ...mapActions('course', ['getBookListWebActions']),
            getNetStatus(){
                let result = ''
                let netLevel = this.devicesStatusObj.net.level
                switch(netLevel){
                    case 1:
                        result = '优秀'
                        break
                    case 2:
                        result = '良好'
                        break
                    case 3:
                        result = '差'
                        break
                    default:
                        result = '-'
                }
                return result
            },
            //切换耳机/音响设备
            setCurHeadPhone(){
                this.audio.setSinkId(this.curHeadPhoneDeviceId)
            },
            //type1.获取耳机设备列表，type2.获取麦克风设备列表
            getMediadevices(type,bol){
                if(submitAble){
                    return
                }
                let submitAble = true
                let refresh = bol===undefined?true:bol
                utils.mediadevice.getDevices().then((data)=>{
                    if(type===1){
                        this.curHeadPhoneDeviceId = ''
                        this.headPhones = data.headphones
                        window.console.log(data.headphones,'音响')
                    }else{
                        this.refreshInputDevice(refresh)
                        this.curInputDeviceId = ''
                        this.inputDeviceList = data.audio
                        window.console.log(data.audio,'麦克风')
                    }
                    submitAble = false
                },()=>{
                    this.$bus.$emit('showWebrtcWarn',true)
                    this.closeDialog()
                    submitAble = false
                })
            },
            //绑定音频的开始和暂停事件
            bindAudioEvent(){
                if(!this.audio){
                    this.audio = document.getElementById('testAudio')
                    this.audio.addEventListener('playing',()=>{
                        this.playingAudio = true
                    })
                    this.audio.addEventListener('pause',()=>{
                        this.playingAudio = false
                    })
                }
            },
            //播放测试音乐
            playAudio(){
//                this.bindAudioEvent()
                if(this.audio!==null){
                    if(this.audio.paused){
                        this.audio.play();
                    }else{
                        this.audio.pause();
                    }
                }
            },
            closeDialog(){
                this.showDialog = false
                this.curStep = 0
                this.$emit('closeCb')
            },
            //恢复声音监听的连接
            connectVoiceProcess(){
                if(this.curStep === 3){
                    if(scriptProcessor && audioContext && !this.hasConnectVoiceApi){
                        this.inputAudio && this.inputAudio.play()
                        scriptProcessor.connect(audioContext.destination);
                        this.hasConnectVoiceApi = true
                    }
                }
            },
            //断开声音监听的连接
            disConnectVoiceProcess(){
                if(this.hasConnectVoiceApi){
                    if(scriptProcessor && audioContext){
                        this.inputAudio && this.inputAudio.pause()
                        scriptProcessor.disconnect(audioContext.destination);
                        this.hasConnectVoiceApi = false
                    }
                }
            },
            //刷新麦克风设备的时候要重新添加一个监听器，直接恢复连接无法重新监听
            refreshInputDevice(bol){
                //是刷新才执行
                if(bol){
                    this.disConnectVoiceProcess()
                    if(scriptProcessor && scriptProcessor.onaudioprocess){
                        scriptProcessor.onaudioprocess = null
                        scriptProcessor = null
                    }
                    this.addVoiceListener()
                }
            },
            //销毁音量监听器
            testNet(){
                if(this.submitAble){
                    return
                }
                this.submitAble = true
                let timeCount = 0
                let startDate = new Date().getTime()
                if(navigator.onLine){
                    //想让圈百分比自动加，最多加到80%，最后百分20需要请求成功后才加到100%
                    let interval = setInterval(()=>{
                        this.circleValue +=2
                        if(this.circleValue>=80){
                            this.circleValue=80
                            clearInterval(interval)
                        }
                    },50)
                    let params = {
                        page_num:1,
                        page_size:5
                    }
                    // 测试网速 随便调用一个接口
                    this.getBookListWebActions(params).then((res)=>{
                        let curDate = new Date().getTime()
                        timeCount = Number(((curDate-startDate)/1000).toFixed(2))
                        //3秒内优秀 3~5秒良好 5秒以上差
                        this.devicesStatusObj.net.passed = true
                        if(timeCount<=3){
                            this.devicesStatusObj.net.level = 1
                        }else if(timeCount>3 && timeCount<=5){
                            this.devicesStatusObj.net.level = 2
                        }else{
                            this.devicesStatusObj.net.level = 3
                        }
                        this.circleValue=100
                        clearInterval(interval)
                        let timer = setTimeout(()=>{
                            this.curStep ++
                            this.submitAble = false
                            clearTimeout(timer)
                        },500)
                    },()=>{
                        this.submitAble = false
                        this.circleValue=0
                        clearInterval(interval)
                        this.$message.error('当前网络不佳，请检查后重试')
                    })

                    /*setTimeout(()=>{
                        let curDate = new Date().getTime()
                        timeCount =parseInt((curDate-startDate)/1000)
                        this.submitAble = false
                        this.circleValue=100
                        clearInterval(interval)
                        alert(timeCount)
                    },4000)*/
                }else{
                    this.submitAble = false
                    this.$message.error('当前没有网络，请检查后重试')
                    this.devicesStatusObj.net.passed = false
//                    this.currentStep = 2
                }
            },
            //测试耳机 type 1:可以 2:不可以
            testOutput(type){
                if(type===1){
                    this.devicesStatusObj.output.passed = true
                }else{
                    this.devicesStatusObj.output.passed = false
                }
                if(this.audio){
                    this.audio.pause();
                }
                this.curStep ++
            },
            //测试麦克风 type 1:可以 2:不可以
            testInput(type){
                if(type===1){
                    this.devicesStatusObj.input.passed = true
                }else{
                    this.devicesStatusObj.input.passed = false
                }
                this.curStep ++
            },
            reTest(){
                this.curStep = 1
            },
            addVoiceListener(){
                let _this = this
                if (navMediaDevices && navMediaDevices.getUserMedia) {
                    // 获取用户的 media 信息
                    let con = {
                        audio: true
                    }
                    navMediaDevices.getUserMedia(con).then((stream) => {
                        if(!this.inputAudio){
                            this.inputAudio = document.getElementById('au')
                            this.inputAudio.srcObject = stream
                        }
                        audioContext = new AudioContext();
                        // 将麦克风的声音输入这个对象
                        let mediaStreamSource = audioContext.createMediaStreamSource(stream);
                        // 创建一个音频分析对象，采样的缓冲区大小为4096，输入和输出都是单声道
                        scriptProcessor = audioContext.createScriptProcessor(4096,1,1);
                        // 将该分析对象与麦克风音频进行连接
                        mediaStreamSource.connect(scriptProcessor);
                        // 此举无甚效果，仅仅是因为解决 Chrome 自身的 bug
                        scriptProcessor.connect(audioContext.destination);
                        // 开始处理音频
                        scriptProcessor.onaudioprocess = function(e) {
                            window.console.log(88888)
                            // 获得缓冲区的输入音频，转换为包含了PCM通道数据的32位浮点数组
                            let buffer = e.inputBuffer.getChannelData(0);
                            // 获取缓冲区中最大的音量值
                            let maxVal = Math.max.apply(Math, buffer);
                            let voiceSize = Math.round(maxVal * 100);
                            _this.voiceSize = Number(voiceSize)
                        };
                        _this.hasConnectVoiceApi = true
                    }).catch((error) => {
                        window.console.log('获取音频时好像出了点问题。' + error);
                    });
                } else {
                    this.$bus.$emit('showWebrtcWarn',true)
                    window.console.log('不支持获取媒体接口');
                }
            },
//            检测完成后写入cookie下次不再提示检测弹窗
            deviceTestWriteToCookie(){
                if(this.accoutInfo && this.accoutInfo.phone){
                    let phone = this.accoutInfo.phone
                    let userKey = 'deviceTest' + phone
                    let t = 24 * 60 * 60 * 30 * 1000
                    let expires = new Date().getTime() + t
                    utils.cookieUtils.setItem(userKey,'true',expires)
//                    document.cookie = Cookie.serialize(userKey, 'true', {path: "/",expires});
                }
            }
        },
        mounted() {

        },
        computed: {
            ...mapGetters('account',{
                accoutInfo: 'info'
            })
        },
        components: {
            step
        },
        watch:{
            curStep:{
                handler(nv){
                    switch(nv){
                        case 0:
                            this.circleValue = 0
                            this.disConnectVoiceProcess()
                            break
                        case 1:
                            this.circleValue = 0
                            this.disConnectVoiceProcess()
                            break
                        case 2:
                            this.$nextTick(()=>{
                                this.bindAudioEvent()
                            })
                            this.disConnectVoiceProcess()
                            this.getMediadevices(1)
                            break
                        case 3:
                            //音量监听
                            if(scriptProcessor){
                                this.connectVoiceProcess()
                            }else{
                                this.addVoiceListener()
                            }
                            this.getMediadevices(2,false)
                            break
                        case 4:
                            this.disConnectVoiceProcess()
                            this.deviceTestWriteToCookie()
                            break
                        default:
                            this.disConnectVoiceProcess()
                    }
                },
                immediate:true
            },
            show:{
                handler(nv){
                    if(nv!==undefined){
                        this.showDialog = nv
                        if(!nv){
                            this.disConnectVoiceProcess()
//                            alert(nv)
                        }
                    }
                },
                immediate:true
            }
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
