<template>
  <div
    class="click-audio-box"
    v-if="mediaType === '2'"
    :style="{ 'margin-top': marginTop }"
  >
    <div class="audio-player">
      <audio class="ellipseAudio" preload="auto">
        <source :src="path" type="audio/mp3" />
      </audio>
      <div class="audio-img" :class="playState === 'play' ? 'play' : ''"></div>
      <div class="time">{{ time }}</div>
    </div>
  </div>
</template>

<script>
//    let audio,audioPlayer,progressBarBg,dot
var lastDateTime = 0;
export default {
  data() {
    return {
      audio: {},
      audioPlayer: null,
      playingAudio: false,
      canplayLoaded: false,
      // 播放状态 默认暂停
      playState: "pause",
    };
  },
  props: {
    path: {
      type: String,
    },
    name: {
      type: String,
      default: "",
    },
    time: {
      type: String,
    },
    //1 图片 2 音频
    mediaType: {
      type: String,
    },
    marginTop: {
      type: Number,
      default: 0.83,
    },
  },
  watch: {},
  methods: {
    audioEnded() {
      this.playState = "pause";
    },
    audioPlaying() {
      this.$bus.$emit("playingAudio", this._uid);
      this.playingAudio = true;
    },
    audioPause() {
      this.playingAudio = false;
    },
    audioCanPlay() {
      //每次重新播都会触发 canplay，本业务中只需要用到第一次所以打个mark
      if (!this.canplayLoaded) {
        this.initAudioEvent(this.audio);
        this.canplayLoaded = true;
      }
    },
    //audio元素 Listeners 结束

    //audio播放按钮 Listeners 开始
    playBtnClick() {
      let curTime = new Date().getTime();
      window.console.log(curTime - lastDateTime, 33333);
      if (curTime - lastDateTime < 100) {
        return;
      }
      // 改变播放/暂停图片
      if (this.audio.paused) {
        // 开始播放当前点击的音频
        this.playState = "play";
        this.audio.play();
      } else {
        this.playState = "pause";
        this.audio.pause();
      }
      lastDateTime = new Date().getTime();
    },
    //audio播放按钮 Listeners 结束

    initAudioEvent(audio) {
      // 点击播放/暂停图片时，控制音乐的播放与暂停
      this.audioPlayer.addEventListener("click", this.playBtnClick, false);
      // 监听播放完成事件
      audio.addEventListener("ended", this.audioEnded, false);
    },

    removeHasBindEveListener() {
      this.audio.removeEventListener("playing", this.audioPlaying);
      this.audio.removeEventListener("pause", this.audioPause);
      this.audio.removeEventListener("canplay", this.audioCanPlay);
      this.audioPlayer.removeEventListener("click", this.playBtnClick);
    },
  },
  mounted() {
    let parent = this.$el;
    if (parent.querySelector) {
      this.audio = parent.querySelector(".ellipseAudio");
      this.audioPlayer = parent.querySelector(".audio-player");
      //如果一个音频正在播放，另一个就要停止
      this.$bus.$on("playingAudio", (uid) => {
        if (uid !== this._uid) {
          this.audio.pause();
          this.playState = "pause";
          this.playingAudio = false;
        }
      });
      this.audio.addEventListener("playing", this.audioPlaying);
      this.audio.addEventListener("pause", this.audioPause);
      this.audio.addEventListener("canplay", this.audioCanPlay);
    }
  },
  destroyed() {
    this.removeHasBindEveListener();
  },
};
</script>

<style scoped lang="postcss">
.click-audio-box {
  display: inline-flex;
  width: 9.17rem;
  height: 2.5rem;
  background: #ffffff;
  border-radius: 1.25rem;
  margin-top: 0.83rem;
  margin-left: 0.56rem;
  padding: 0;
  &:nth-child(7n + 1) {
    margin-left: 0rem;
  }
  &:first-of-type {
    margin-left: 0rem;
  }
  & .audio-player {
    display: flex;
    align-items: center;
    & .audio-img {
      background: url("~@/assets/img/ico-play.png") no-repeat center;
      background-size: 100% 100%;
      margin-left: 0.83rem;
      width: 1.42rem;
      height: 1.42rem;
    }
    & .play {
      background: url("~@/assets/img/ico-play.gif") no-repeat center;
      background-size: 100% 100%;
    }
    & .time {
      margin-left: 4.4rem;
      height: 1.17rem;
      font-size: 1rem;
      font-family: SFUIDisplay-Regular, SFUIDisplay;
      font-weight: 400;
      color: #999999;
      line-height: 1.17rem;
    }
  }
}
</style>
